.cabecera {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  margin-bottom: 50px;
}

.titulo {
  padding: 30px 0 40px 0;
  font-family: 'Reenie Beanie';
  font-size: 70px;
}

.formulario {
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.boton {
  margin: 2px;
  padding: 2px;
}

body {
  background: url("../images/pokeballs.jpg") center center no-repeat #fff;
  background-position: top;
}

.listadelis {
  padding: 0;
  display: flex;
  flex-direction: row;
  list-style: none;
  flex-wrap: wrap;
  justify-content: center;
}

.escalado {
  width: 160px;
  height: 195px;
}

.claseoculta {
  display: none;
}

.li {
  margin: 30px 20px;
  border: 2px solid blue;
  background-color: #fff;
  width: 160px;
  height: 195px;
}
